// fonts
$font-primary: 'Poppins', sans-serif;
$font-secondary: 'Plus Jakarta Sans', sans-serif;


// font weights
$regular: 400;
$medium: 500;
$bold: 600;
$extra-bold: 800;

// colors
$white: #ffffff;
$black: #000000;
$black-secondary: #181818;
$dark-grey: #262a2c;
$gradient: linear-gradient(180deg, #4b34e4 0%, #ee346b 100%);

// transition
$transition: 250ms ease-in-out;
